@import './variables.scss';

.--button {
  cursor: pointer;

  .--button-text {
    color: #FFF;

    &:hover {
      color: $accent-color;
    }
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.accented {
  color: $accent-color;
}