.HomepageNav {
  &.--container {
    width: 60%;
    padding: 20px 0;
  }

  .--nav {
    &.-options {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
    }

    &.-option,
    &.-info {
      color: #FFF;
    }

    &.-option {
      padding: 0 15px;
      margin: 0;
      font-weight: 700;

      &:hover {
        color: $accent-color;
      }
    }

    &.-info {
      text-align: right;
      padding-right: 15px;
      min-width: 95%;
    }
  }
}

@media screen and (max-width: 540px) {
  .HomepageNav { 
    .--nav {
      &.-info {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 588px) {
  .HomepageNav {
    .--nav {
      &.-info {
        font-size: 12px;
      }
    }
  }
}
